import React from 'react';
import PropTypes from 'prop-types';
import './App.scss';

import DetailsModal from './DetailsModal';
import Button from './Button';
import BookPanel from './BookPanel';
import Banner from './Banner';
import SelectABook from './SelectABook';
import FancySeparator from './FancySeparator';
import { getCheckoutUrl } from './utils.js';

export default class App extends React.Component {
  static propTypes = {
    defaultBook: PropTypes.object,
  };

  constructor(props) {
    super(props);

    this.state = {
      // You can choose up to two books.
      leftBook: this.props.defaultBook || undefined,
      rightBook: undefined,

      // Tracks whether we're currently displaying just the default book.
      // If a default book is specified, we want the first thing the user sees to be a page about that.
      displayingJustDefaultBook: Boolean(this.props.defaultBook),

      // Which book are we displaying book details for?
      displayingBookDetailsFor: undefined,
    };
  }

  render() {
    let content;
    if (this.state.displayingJustDefaultBook) {
      content = (
        <div className="book-panel-container">
          <BookPanel
            book={this.props.defaultBook}
          />

          <div style={{ marginTop: '3rem', display: 'flex' }}>
            <Button
              primaryColor='black'
              secondaryColor='white'
              flipTextColor={true}
              extraSpanStyles={{ width: '100%' }}
              href={getCheckoutUrl()}
            >
              <span>
                <span>Get Just This Book</span>
                <br/>
                <span style={{ fontSize: '10pt' }}>
                  (physical copy shipped to you, ebook, or audiobook)
                </span>
              </span>
            </Button>

            <Button
              primaryColor='black'
              secondaryColor='white'
              flipTextColor={true}
              extraSpanStyles={{ width: '100%', marginLeft: '1rem' }}
              onClick={this.onClickGetAnotherBook.bind(this)}
            >
              Choose An Additional Book
            </Button>
          </div>
        </div>
      );
    } else {
      content = (
        <SelectABook
          booksSelected={[this.state.leftBook, this.state.rightBook]}
          onBookAdded={this.onBookAdded.bind(this)}
          onBookRemoved={this.onBookRemoved.bind(this)}
          startDisplayingBookDetailsFor={this.startDisplayingBookDetailsFor.bind(this)}
        />
      );
    }

    let banner, spacingDivForBanner, bookDetailsModal;
    if (this.state.displayingBookDetailsFor) {
      bookDetailsModal = (
        <DetailsModal
          book={this.state.displayingBookDetailsFor}
          inBasket={[this.state.leftBook, this.state.rightBook].includes(this.state.displayingBookDetailsFor)}
          onBookAdded={this.onBookAdded.bind(this)}
          onBookRemoved={this.onBookRemoved.bind(this)}
          closeModal={this.stopDisplayingBookDetails.bind(this)}
        />
      );
    } else {
      banner = (
        <Banner
          leftBook={this.state.leftBook}
          rightBook={this.state.rightBook}
          onBookRemoved={this.onBookRemoved.bind(this)}
          startDisplayingBookDetailsFor={this.startDisplayingBookDetailsFor.bind(this)}
        />
      );
      spacingDivForBanner = <div className="spacing-div-for-banner"></div>;
    }

    return (
      <div>
        {banner}

        <div className="app">
          {spacingDivForBanner}

          <div style={{ paddingTop: '2rem', marginBottom: '4rem' }}>
            <div style={{textAlign: 'center', marginBottom: '3rem'}}>
              <h1 className="title">
                Navigator's Library
              </h1>

              <div className="navbar" style={{ fontVariant: 'small-caps' }}>
                <a href="#about">
                  About
                </a>

                <span style={{ marginLeft: '0.5rem', marginRight: '0.5rem'}}>
                  •
                </span>

                <a href="#links">
                  Links
                </a>
              </div>
            </div>
          </div>

          {content}
          {bookDetailsModal}

          <FancySeparator/>

          <div id='about' style={{ marginTop: '2rem', padding: '2rem'}}>
            <h2>About Navigator's Library</h2>

            <p>
              <h3>What is Navigator's Library?</h3>
              <div>
                <p>
                    Fermentum posuere urna nec tincidunt. Scelerisque eu ultrices vitae auctor eu augue ut. Consequat nisl vel pretium lectus quam id leo. Sed blandit libero volutpat sed cras ornare arcu dui. Id consectetur purus ut faucibus pulvinar elementum. At volutpat diam ut venenatis tellus in metus. Tristique senectus et netus et malesuada fames ac. Viverra nibh cras pulvinar mattis nunc sed blandit. Lobortis feugiat vivamus at augue eget arcu. Sit amet aliquam id diam. At augue eget arcu dictum varius duis at. Auctor urna nunc id cursus metus aliquam eleifend mi. Vestibulum mattis ullamcorper velit sed ullamcorper. Mauris sit amet massa vitae tortor condimentum lacinia. Tellus orci ac auctor augue mauris augue neque gravida. Dignissim sodales ut eu sem integer vitae justo eget. Erat nam at lectus urna. Quis eleifend quam adipiscing vitae. Sed tempus urna et pharetra.
                </p>
              </div>

              <h3>Who's behind this?</h3>
              <div>
                <p>
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
                </p>
              </div>

              <h3>Why is it free?</h3>
              <div>
                <p>
                    In fermentum posuere urna nec tincidunt praesent. Pharetra massa massa ultricies mi quis hendrerit dolor magna eget. Viverra aliquet eget sit amet tellus cras adipiscing enim eu. Laoreet suspendisse interdum consectetur libero id faucibus nisl tincidunt. Eu augue ut lectus arcu. Mi quis hendrerit dolor magna eget est lorem ipsum. Amet justo donec enim diam vulputate ut pharetra sit amet. Tincidunt praesent semper feugiat nibh sed pulvinar proin gravida hendrerit. Elementum nibh tellus molestie nunc non. Praesent semper feugiat nibh sed pulvinar proin gravida. Mattis vulputate enim nulla aliquet porttitor lacus luctus accumsan tortor. Senectus et netus et malesuada fames ac turpis. Magna fermentum iaculis eu non diam phasellus vestibulum lorem sed.
                </p>
              </div>

              <h3>How does it work?</h3>
              <div>
                <p>
                    In ante metus dictum at tempor commodo ullamcorper a lacus. Enim diam vulputate ut pharetra. Malesuada fames ac turpis egestas maecenas pharetra convallis posuere morbi. Ultricies tristique nulla aliquet enim tortor at auctor urna nunc. Aenean sed adipiscing diam donec adipiscing tristique risus nec. Orci sagittis eu volutpat odio facilisis mauris sit amet. Eu mi bibendum neque egestas congue quisque egestas diam. Habitasse platea dictumst quisque sagittis purus sit amet volutpat consequat. Tempus imperdiet nulla malesuada pellentesque elit eget. Consectetur lorem donec massa sapien faucibus et molestie ac feugiat. Magna sit amet purus gravida quis blandit turpis cursus. In hendrerit gravida rutrum quisque non tellus orci ac.
                </p>
              </div>
            </p>
          </div>

          <FancySeparator/>

          <div id='links' style={{ padding: '2rem'}}>
            <h2>Links</h2>

            <div>
              <p>
                Some of our favorite links, maybe.
              </p>
            </div>
          </div>
        </div>
      </div>
    );
  }

  onClickGetAnotherBook = () => {
    // We want to scroll back to the top after exiting the default book display.
    this.setState({ displayingJustDefaultBook: false }, () => window.scrollTo(0, 0));
  }

  onBookAdded(newBook) {
    // Put in the left slot only if that slot is empty; otherwise, we put it in the right slot (replacing whatever's there).
    if (this.state.leftBook) {
      this.setState({ rightBook: newBook });
    } else {
      this.setState({ leftBook: newBook });
    }
  }

  onBookRemoved(toBeRemoved) {
    // Remove by filtering out the book, then sliding everything down by one.
    const afterRemoval = [this.state.leftBook, this.state.rightBook].filter((b) => b && b.name !== toBeRemoved.name);
    const afterSliding = afterRemoval.filter((e) => e !== undefined);

    this.setState({ leftBook: afterSliding[0], rightBook: afterSliding[1] });
  }

  startDisplayingBookDetailsFor(book) {
    this.setState({ displayingBookDetailsFor: book });
  }

  stopDisplayingBookDetails() {
    this.setState({ displayingBookDetailsFor: undefined });
  }
}