import React from 'react';
import PropTypes from 'prop-types';
import ReactModal from 'react-modal';

import Button from './Button';
import BookPanel from './BookPanel';
import { gray, green } from './colors.js';

export default class DetailsModal extends React.Component {
  static propTypes = {
    // The book this is about.
    book: PropTypes.object,

    // Is the book already in the basket?
    inBasket: PropTypes.bool.isRequired,

    onBookAdded: PropTypes.func,
    onBookRemoved: PropTypes.func,
    closeModal: PropTypes.func.isRequired,
  }

  render(book) {
    if (!this.props.book) {
      return null;
    }

    return (
      <ReactModal
        isOpen={true}
        onRequestClose={this.props.closeModal}
        style={{
          overlay: { maxWidth: '70rem', margin: '0 auto' },
          content: { inset: '0.5rem', backgroundColor: gray },
        }}

        // Prevent the rest of the page scrolling while the modal is open.
        onAfterOpen={() => document.body.style.overflow = 'hidden'}
        onAfterClose={() => document.body.style.overflow = 'unset'}
      >
        {this.renderXButton()}

        <BookPanel book={this.props.book} />

        <div style={{ display: 'flex', marginTop: '3rem' }}>
          <Button
            primaryColor={this.props.inBasket ? 'black' : green}
            secondaryColor='white'
            flipTextColor={true}
            onClick={this.props.inBasket ? () => { this.props.onBookRemoved(this.props.book); this.props.closeModal() } : () => { this.props.onBookAdded(this.props.book); this.props.closeModal(); }}

            extraSpanStyles={{ width: '100%' }}
          >
            {this.props.inBasket ? 'Remove from basket' : 'Add to basket'}
          </Button>

          <Button
            primaryColor='black'
            secondaryColor='white'
            flipTextColor={true}
            onClick={this.props.closeModal}

            extraSpanStyles={{ width: '100%',  marginLeft: '1rem' }}
          >
            Go back
          </Button>
        </div>
      </ReactModal>
    );
  }

  renderXButton() {
    return (
      <div style={{ display: 'flex', justifyContent: 'end', alignItems: 'center', marginBottom: '1rem' }}>
        <Button
          primaryColor={gray}
          secondaryColor='black'
          onClick={this.props.closeModal}
          extraSpanStyles={{ width: '1rem', height: '1rem', fontSize: '10pt' }}
        >
          ⨉
        </Button>
      </div>
    );
  }
}