import React from 'react';
import PropTypes from 'prop-types';
import './Button.scss';

export default class Button extends React.Component {
  static propTypes = {
    primaryColor: PropTypes.string,
    secondaryColor: PropTypes.string,
    flipTextColor: PropTypes.bool,

    onClick: PropTypes.func,

    // Optional.
    href: PropTypes.string,

    // CSS styles to be applied to the <span> element.
    extraSpanStyles: PropTypes.object,
  }

  constructor(props) {
    super(props);

    this.state = {
      isHovered: false,
    }
  }

  render() {
    let color;
    if (this.state.isHovered) {
      color = (this.props.flipTextColor ? 'black' : 'white');
    } else {
      color = (this.props.flipTextColor ? 'white' : 'black');
    }

    const backgroundColor = this.state.isHovered ? this.props.secondaryColor: this.props.primaryColor;
    return (
      <span className="button" style={this.props.extraSpanStyles || {}}>
        <a
          onMouseEnter={() => this.setState({ isHovered: true, })}
          onMouseLeave={() => this.setState({ isHovered: false, })}
          onClick={this.props.onClick}
          href={this.props.href}
          style={{ backgroundColor, color }}
        >
          {this.props.children}
        </a>
      </span>
    );
  }
}