import React from 'react';
import PropTypes from 'prop-types';
import './Banner.scss';

import DetailsModal from './DetailsModal';
import Button from './Button';
import { getCheckoutUrl } from './utils.js';

export default class Banner extends React.Component {
  static propTypes = {
    leftBook: PropTypes.object,
    rightBook: PropTypes.object,

    startDisplayingBookDetailsFor: PropTypes.func,
    onBookRemoved: PropTypes.func,
  };

  constructor(props) {
    super(props);

    this.state = {
      // Book we're displaying details for, if applicable (else null).
      // Optional<Book>
      displayingDetailsFor: null,
    }
  }

  render() {
    const leftBook = this.props.leftBook;
    const rightBook = this.props.rightBook;

    return (
      <div className="banner">
        <div style={{ marginBottom: '1rem', marginLeft: '0.5rem', marginRight: '0.5rem' }}>
          <div style={{ marginBottom: '0.5rem', textAlign: 'center' }}>
            <h3 style={{ fontVariant: 'small-caps', marginBottom: '0' }}>
              Your Basket
            </h3>

            <div style={{ fontSize: '10pt' }}>Choose up to 2 free books.</div>
          </div>

          <div style={{ display: 'grid', gridTemplateColumns: '45% 45%', height: '5.5rem', justifyContent: 'space-around' }}>
            {leftBook ? this.renderBookInBasket(leftBook) : this.renderEmptySlot()}
            {rightBook ? this.renderBookInBasket(rightBook) : this.renderEmptySlot()}
          </div>

          <Button
            primaryColor='black'
            secondaryColor='white'
            flipTextColor={true}
            extraSpanStyles={{ height: '1.5rem', marginTop: '1.5rem' }}
            href={getCheckoutUrl()}
          >
            Checkout
          </Button>
        </div>
      </div>
    );
  }

  renderBookInBasket(book) {
    return (
      <div>
        <div style={{ display: 'flex', alignItems: 'center', padding: '0.25rem', height: '3rem' }}>
          <img style={{ width: '2rem', marginRight: '0.5rem' }} src={book.coverImage}/>

          <span style={{ fontWeight: 'bold', fontStyle: 'italic', wordWrap: 'anywhere', fontSize: '10pt' }}>
            {book.name}
          </span>
        </div>

        <div style={{ display: 'flex', justifyContent: 'center', marginTop: '0.75rem' }}>
          <Button
            primaryColor='black'
            secondaryColor='white'
            flipTextColor={true}
            extraSpanStyles={{ width: '3rem', height: '0.25rem', fontSize: '8pt' }}

            onClick={() => this.props.onBookRemoved(book)}
          >
            Remove
          </Button>

          <Button
            primaryColor='black'
            secondaryColor='white'
            flipTextColor={true}
            extraSpanStyles={{ marginLeft: '0.5rem', width: '5rem', height: '0.25rem', fontSize: '8pt' }}

            onClick={() => this.props.startDisplayingBookDetailsFor(book)}
          >
            See details
          </Button>
        </div>
      </div>
    );
  }

  renderEmptySlot() {
    return (
      <div>
      </div>
    )
  }
};
