function getCheckoutUrl(extraQueryParams = {}) {
  const queryParams = {
    // Make the 'receive emails from us' box checked by default.
    'prefill_Opted into emails': '1',
  };

  Object.assign(queryParams, extraQueryParams); // Mutates the first argument.
  const queryParamString = new URLSearchParams(queryParams).toString();

  return `https://airtable.com/shrm2ftp95vfcjlFS?${queryParamString}`;
}

export { getCheckoutUrl };