import React from 'react';
import ReactDOM from 'react-dom';
import Modal from 'react-modal';
import './index.scss';
import { AllBooks } from './books';
import App from './App';

// Get the default book from the query params, if it's there.
let defaultBook;
let defaultBookName = new URLSearchParams(window.location.search).get('book');
if (defaultBookName) {
  defaultBookName = decodeURIComponent(defaultBookName);
  defaultBook = AllBooks.find((book) => book.name === defaultBookName);
}

// Set up React Modal.
Modal.setAppElement('#root');

ReactDOM.render(
  <React.StrictMode>
    <App defaultBook={defaultBook}/>
  </React.StrictMode>,
  document.getElementById('root')
);
