import React from 'react';
import PropTypes from 'prop-types';
import './BookPanel.scss';

import FancySeparator from './FancySeparator';

export default class BookPanel extends React.Component {
  static propTypes = {
    book: PropTypes.object.isRequired,
  };

  render() {
    return (
      <div className="book-panel">
        <div style={{ display: 'flex' }} className="cover-and-quote-container">
          <div className="cover-container">
            <img className="cover" src={this.props.book.coverImage}/>
          </div>

          <div className="pull-quote-container">
            <div>
              {/* Sometimes this text contains cosmetic HTML tags. It's safe to set inner HTML here because we control the text. */}
              <div style={{fontSize: '20pt', fontWeight: 'bold'}} dangerouslySetInnerHTML={{ __html: `“${this.props.book.pullQuote.text}”`}}>
              </div>

              {/* Sometimes this text contains cosmetic HTML tags. It's safe to set inner HTML here because we control the text. */}
              <div style={{marginTop: '1rem', textAlign: 'right', marginLeft: 'auto', maxWidth: '20rem'}} dangerouslySetInnerHTML={{ __html: `— ${this.props.book.pullQuote.byline}`}}>
              </div>
            </div>

            <div className="summary-container">
              {this.renderSummary(this.props.book.summaryParagraphs)}
            </div>
          </div>
        </div>

        {this.renderQuotesSectionIfNecessary()}
      </div>
    );
  }

  renderSummary(summaryParagraphs) {
    // Some of the summary text contains cosmetic HTML tags. It's safe to set inner HTML here because we control the text.
    const ps = summaryParagraphs.map(
      (text, i) => <p key={i} dangerouslySetInnerHTML={{__html: text}}></p>
    );

    return (
      <div>
        {ps}
      </div>
    );
  }

  renderQuotesSectionIfNecessary() {
    if (this.props.book.quotes.length === 0) {
      return null;
    }

    return (
      <div>
        <FancySeparator/>

        <h2 style={{ textAlign: 'center', marginTop: '2rem' }}>Quotes</h2>
        {this.props.book.quotes.map( ({ text, byline }) => this.renderQuote(text, byline) )}
      </div>
    );
  }

  renderQuote(text, byline) {
    return (
      <div key={text} style={{ marginTop: '2rem' }}>
        {/* Sometimes this text contains cosmetic HTML tags. It's safe to set inner HTML here because we control the text. */}
        <div dangerouslySetInnerHTML={{ __html: `“${text}”`}}>
        </div>

        {/* Sometimes this text contains cosmetic HTML tags. It's safe to set inner HTML here because we control the text. */}
        <div style={{ marginTop: '1rem', textAlign: 'right', marginLeft: 'auto'}} dangerouslySetInnerHTML={{ __html: `— ${byline}`} }>
        </div>
      </div>
    );
  }
}