import React from 'react';
import PropTypes from 'prop-types';
import './SelectABook.scss';

import DetailsModal from './DetailsModal';
import Button from './Button';
import { AllBooks } from './books.js';
import { green } from './colors.js';

export default class SelectABook extends React.Component {
  static propTypes = {
    booksSelected: PropTypes.arrayOf(PropTypes.object).isRequired,

    startDisplayingBookDetailsFor: PropTypes.func.isRequired,

    onBookAdded: PropTypes.func.isRequired,
    onBookRemoved: PropTypes.func.isRequired,
  }

  render() {
    // Don't show the books that are already selected.
    const booksToDisplay = AllBooks.filter((book) => !this.props.booksSelected.includes(book));

    return (
      <div className="select-a-book">
        <div className="grid">
          {booksToDisplay.map(this.renderBook.bind(this))}
        </div>
      </div>
    );
  }

  renderBook(book) {
    return (
      <div key={book.name} className="cover-container">
        <img src={book.coverImage} className="cover"/>

        <div className="read-more-options">
          <Button
            primaryColor={green}
            secondaryColor='white'
            flipTextColor={true}
            onClick={() => this.props.onBookAdded(book)}
            extraSpanStyles={{ height: '2rem' }}
          >
            Add to basket
          </Button>

          <Button
            primaryColor='black'
            secondaryColor='white'
            flipTextColor={true}
            onClick={() => this.props.startDisplayingBookDetailsFor(book)}
            extraSpanStyles={{ height: '2rem' }}
          >
            See details
          </Button>
        </div>
      </div>
    );
  }
}